import React from 'react';
import { BrowserRouter as Router, HashRouter as HashRouter, Routes, Route, Navigate, useLocation } from 'react-router-dom';
import { Toaster } from "@/components/ui/toaster";
import { Toaster as Sonner } from "@/components/ui/sonner";
import { TooltipProvider } from "@/components/ui/tooltip";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { useEffect } from "react";

import { AuthProvider } from "@/contexts/AuthContext";
import { BankingProvider } from "@/contexts/BankingContext";
import { useGitHubPagesRedirect, useIsGitHubPages } from "@/utils/router";

// Pages
import Index from "./pages/Index";
import Login from "./pages/Login";
import Register from "./pages/Register";
import Dashboard from "./pages/Dashboard";
import Transfers from "./pages/Transfers";
import Cards from "./pages/Cards";
import Investments from "./pages/Investments";
import Loans from "./pages/Loans";
import Mortgage from "./pages/Mortgage";
import NotFound from "./pages/NotFound";
import Accounts from "./pages/Accounts";

// Layout
import Layout from "./components/Layout";

const queryClient = new QueryClient();

// Protected Route component
const ProtectedRoute = ({ children }: { children: React.ReactNode }) => {
  const isAuthenticated = localStorage.getItem('token'); // Replace with your auth logic
  return isAuthenticated ? <>{children}</> : <Navigate to="/login" />;
};

// Route handler component
const RouteHandler = () => {
  useGitHubPagesRedirect();
  return null;
};

// Router component that chooses the appropriate router based on the environment
const AppRouter = ({ children }: { children: React.ReactNode }) => {
  const isGitHubPages = useIsGitHubPages();
  
  if (isGitHubPages) {
    return <Router basename="/testimbank">{children}</Router>;
  } else {
    return <HashRouter>{children}</HashRouter>;
  }
};

const App = () => (
  <QueryClientProvider client={queryClient}>
    <AuthProvider>
      <BankingProvider>
        <TooltipProvider>
          <Toaster />
          <Sonner />
          <AppRouter>
            <RouteHandler />
            <Routes>
              {/* Public routes */}
              <Route path="/" element={<Index />} />
              <Route path="/login" element={<Login />} />
              <Route path="/register" element={<Register />} />
              
              {/* Protected routes */}
              <Route path="/dashboard" element={<ProtectedRoute><Dashboard /></ProtectedRoute>} />
              <Route path="/transfers" element={<ProtectedRoute><Transfers /></ProtectedRoute>} />
              <Route path="/accounts" element={<ProtectedRoute><Accounts /></ProtectedRoute>} />
              <Route path="/cards" element={<ProtectedRoute><Cards /></ProtectedRoute>} />
              <Route path="/investments" element={<ProtectedRoute><Investments /></ProtectedRoute>} />
              <Route path="/loans" element={<ProtectedRoute><Loans /></ProtectedRoute>} />
              <Route path="/mortgage" element={<ProtectedRoute><Mortgage /></ProtectedRoute>} />
              
              {/* Catch-all route */}
              <Route path="*" element={<NotFound />} />
            </Routes>
          </AppRouter>
        </TooltipProvider>
      </BankingProvider>
    </AuthProvider>
  </QueryClientProvider>
);

export default App;
