import { useLocation, Link, useNavigate } from "react-router-dom";
import { useEffect } from "react";
import { useIsGitHubPages } from "@/utils/router";

const NotFound = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const isGitHubPages = useIsGitHubPages();

  useEffect(() => {
    // If we're at the root path, redirect to home
    if (isGitHubPages && (location.pathname === '/' || location.pathname === '/testimbank/')) {
      navigate('/', { replace: true });
    } else {
      console.error(`404 Error: User attempted to access non-existent route: ${isGitHubPages ? location.pathname : location.hash}`);
    }
  }, [location, navigate, isGitHubPages]);

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50">
      <div className="max-w-md w-full space-y-8 p-8 bg-white rounded-lg shadow-lg">
        <div className="text-center">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">404</h1>
          <h2 className="text-2xl font-semibold text-gray-700 mb-4">Page Not Found</h2>
          <p className="text-gray-600 mb-8">
            The page you're looking for doesn't exist or has been moved.
          </p>
          <Link to="/" className="text-blue-500 hover:text-blue-700 underline">
            Return to Home
          </Link>
        </div>
      </div>
    </div>
  );
};

export default NotFound;
