import { useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';

/**
 * Custom hook to handle GitHub Pages routing issues
 * This will detect if we're running on GitHub Pages and redirect from /testimbank/ to the home page
 */
export const useGitHubPagesRedirect = () => {
  const location = useLocation();
  const navigate = useNavigate();
  
  useEffect(() => {
    // Check if we're running on GitHub Pages
    const isGitHubPages = window.location.hostname.includes('github.io');
    
    // If we're on GitHub Pages and at the root path, redirect to home
    if (isGitHubPages && location.pathname === '/testimbank/') {
      navigate('/', { replace: true });
    }
  }, [location, navigate]);
};

/**
 * Custom hook to determine if we're running on GitHub Pages
 */
export const useIsGitHubPages = () => {
  return window.location.hostname.includes('github.io');
}; 